import React from 'react';
import classNames from 'classnames';
import { Link } from 'react-router-dom';
import Image from '../../elements/Image';

const Logo = ({
  className,
  ...props
}) => {

  const classes = classNames(
    'brand',
    className
  );

  return (
    <div
      {...props}
      className={classes}
    >
      <h1 className="m-0" style={{"margin-top":"10px"}}>
        <a href="/">
          <Image
            src={require('./../../../assets/images/logo.png')}
            alt="Open"
            width={200}
            height={200} 
            fill="purple"/>
        </a>
      </h1>
    </div>
  );
}

export default Logo;